.controls {
  position: relative;
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;

  & .pause {
    display: inline-block;
    background-color: darken(colors(accent), 15);
    border-radius: 3.5rem;
    transition: all 3s ease-in-out;
    cursor: pointer;

    & img {
      padding: 1rem;
      width: 3.5rem;

      &:hover {
        transition: all 1s ease-in-out;
        transform: scale(1.3);
      }
    }
  }
}

.tracker {
  margin-top: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
  text-align: center;
  margin-bottom: 2.5rem;

  & p {
    font-size: 1.8rem;
    opacity: 0.6;
  }
  & h4 {
    padding: 0;
    margin: 0;
  }
}

.clicked {
  content: url("../img/pause.svg");
  transition: opacity 3s ease-in-out;
}
